.cursor {
  z-index: 99;
  pointer-events: none;
  position: fixed;
  opacity: 0;
  width: 5rem;
  height: 5rem;
  left: -2.6rem;
  top: -2.6rem;
  transition: transform 0.4s cubic-bezier(0.3, 1, 0.7, 1);
}
.cursor__default {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  border-radius: 100%;
  border: 2px solid var(--secondary-color);
  transition: transform 0.2s cubic-bezier(0.3, 1, 0.7, 1);
}
.cursor.--active .cursor__default {
  transform: scale(0.8);
}
.cursor__mask {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%);
  width: 5px;
  height: 5px;
  opacity: 0;
  border-radius: 100%;
  background-color: var(--secondary-color);
  transition: transform 0.4s cubic-bezier(0.3, 1, 0.7, 1), opacity 0.4s cubic-bezier(0.3, 1, 0.7, 1);
}
.cursor.--small .cursor__mask, .cursor.--medium .cursor__mask, .cursor.--large .cursor__mask {
  opacity: 1;
}
.cursor.--small .cursor__mask {
  transform: translate(-50%) scale(13);
}
.cursor.--medium .cursor__mask {
  transform: translate(-50%) scale(25);
}
.cursor.--large .cursor__mask {
  transform: translate(-50%) scale(45);
}
.cursor.--small.--active .cursor__mask {
  transform: translate(-50%) scale(9);
}
.cursor.--medium.--active .cursor__mask {
  transform: translate(-50%) scale(17);
}
.cursor.--large.--active .cursor__mask {
  transform: translate(-50%) scale(35);
}

